import { getDesigners, getWorks, getProjects } from '@/api/index'

const index = {
  state: {
    loading_w: true,
    loading_p: true,
    loading_d: true,
    designers: [],
    works: [],
    projects: []
  },

  mutations: {
    SET_DESIGNERS: (state, designers) => {
      state.designers = designers
    },
    SET_WORKS: (state, works) => {
      state.works = works
    },
    SET_PROJECTS: (state, projects) => {
      state.projects = projects
    },
    SET_LOADING_W: (state, loading) => {
      state.loading_w = loading
    },
    SET_LOADING_P: (state, loading) => {
      state.loading_p = loading
    },
    SET_LOADING_D: (state, loading) => {
      state.loading_d = loading
    }
  },

  actions: {
    getIndexDesigners ({ commit, state }) {
      if (state.designers.length) return Promise.resolve()

      commit('SET_LOADING_D', true)
      return getDesigners()
        .then(({ data: { data } }) => {
          commit('SET_DESIGNERS', data)
          commit('SET_LOADING_D', false)
        })
        .catch(() => {
          commit('SET_LOADING_D', false)
        })
    },
    getIndexWorks ({ commit, state }) {
      if (state.works.length) return Promise.resolve()

      commit('SET_LOADING_W', true)
      return getWorks()
        .then(({ data }) => {
          commit('SET_WORKS', data)
          commit('SET_LOADING_W', false)
        })
        .catch(() => {
          commit('SET_LOADING_W', false)
        })
    },
    getIndexProjects ({ commit, state }) {
      if (state.projects.length) return Promise.resolve()

      commit('SET_LOADING_P', true)
      return getProjects()
        .then(({ data: { data } }) => {
          commit('SET_PROJECTS', data)
          commit('SET_LOADING_P', false)
        })
        .catch(() => {
          commit('SET_LOADING_P', false)
        })
    }
  }
}

export default index



